.countdown-wrapper {
    font-family: 'Lato', sans-serif;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-wrap: wrap;
}

.countdown-item {
    color: #111;
    font-size: 15px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-direction: column;
    /* line-height: 30px; */
    /* margin: 10px; */
    padding: 10px;
    border: 2px solid #e9ecef;
    position: relative;
    width: 40px;
    height: 40px;
}

.countdown-item span {
    color: #333;
    font-size: 12px;
    font-weight: 600;
    text-transform: uppercase;
}
.for_fontsize{
    font-size: 15px !important;
}

.statement_wrapper{
    font-size: 15px !important;
}
.is-active {
  font-weight: bold;
}
.content-container {
  margin: 0 auto;
  max-width: 80rem;
  padding: 0 1.6rem;
}

